import { XMarkIcon } from '@heroicons/react/24/solid'

const ListCard = () => {
  return (
    <div className="flex mt-24 list-card overflow-hidden w-full relative">
      <div className="flex flex-col p-[16px] bg-pink border-r border-black">
        <span className="font-bricolage-32-extrabold leading-7">10</span>
        <span className="font-archivo-14-normal">Items</span>
      </div>
      <div className="flex flex-col p-[16px] w-full bg-white">
        <span className="font-bricolage-20-semibold leading-7 pink">Monday</span>
        <div className="flex justify-between w-full items-end">
          <div className="font-archivo-12-normal">January 2, 2025</div>
          <div className="font-archivo-14-normal">
            Total of <b>Php 1450</b>
          </div>
        </div>
      </div>
      <XMarkIcon
        className="absolute stroke-current stroke-2 right-10 top-2"
        height={18}
      />
    </div>
  )
}

export default ListCard
